<template lang="html">
  <div>
    <Header></Header>
    <vs-popup
      style="color:#ffffff"
      background-color="rgba(255,255,255,.6)"
      :background-color-popup="colorx"
      title="Bienvenido"
      :active.sync="popupActivo5"
    >
      <p>
        Este sistema pretende calcular una ruta optima con su origen y destino,
        tratando de evitar puntos en la pantalla
      </p>
    </vs-popup>

    <vs-row vs-justify="center">
      <vs-col vs-w="11">
        <vs-card>
          <vs-alert title="INFORMACIN" active="true">
            Para calcular la ruta optima en el mapa, mueva los iconos de
            posicion que estn en la pantalla.
          </vs-alert>
          <vs-row vs-justify="center">
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Punto de origen
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/download.png"
                    style="width: 11%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >El icono ubicado del lado izquierdo del mapa indica ser el
                    punto de origen</span
                  >
                </div>
              </vs-card>
            </vs-col>
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Encuentra tu sector
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/cuadrante1.png"
                    style="width: 18%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >Mueva el icono para encotrar el sector en el que te
                    encuentras</span
                  >
                </div>
              </vs-card>
            </vs-col>
            <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="4">
              <vs-card actionable class="cardx">
                <div slot="header">
                  <h3>
                    Punto de destino
                  </h3>
                </div>
                <div slot="media">
                  <img
                    src="static/images/download.png"
                    style="width: 11%;margin-left: auto;margin-right: auto;"
                  />
                </div>
                <div>
                  <span
                    >El icono de posicion ubicado del lado derecho en el mapa,
                    indica ser el punto de destino</span
                  >
                </div>
              </vs-card>
            </vs-col>
          </vs-row>

          <!-- <vs-row>
            <vs-col vs-lg="6" vs-xs="6" vs-justify="space-around">
              <div class="text-left">
                <ValidationObserver v-slot="{ invalid }">
                  <form @submit.prevent="handleSubmit(agregar_elemento)">
                    <ValidationProvider
                      name="Longitud"
                      :rules="{ required: true, min: 1, max: 5 }"
                      v-slot="{ errors }"
                    >
                      <h5 class="titulos">Origen</h5>

                      <vs-input
                        style="width:90%"
                        :success="!invalid"
                        label="Introduce la direccion de tu origen"
                        placeholder="Longitud"
                        v-model="vmin"
                      />
                      <span>{{ errors[0] }}</span>
                    </ValidationProvider>
                  </form>
                  <br />
                  <vs-button
                    color="success"
                    type="filled"
                    :disabled="invalid"
                    @click="agregar_elemento"
                    >Agregar direccin</vs-button
                  >
                </ValidationObserver>
              </div>
            </vs-col>
            <vs-col vs-lg="6" vs-xs="6" vs-justify="space-around">
              <div class="text-left">
                <ValidationObserver v-slot="{ invalid }">
                  <form @submit.prevent="handleSubmit(agregar_elemento)">
                    <ValidationProvider
                      name="Longitud"
                      :rules="{ required: true, min: 1, max: 5 }"
                      v-slot="{ errors }"
                    >
                      <h5 class="titulos">Destino</h5>
                      <vs-input
                        style="width:90%"
                        :success="!invalid"
                        label="Introduce la direccion de tu origen"
                        placeholder="Longitud"
                        v-model="vmin"
                      />
                      <span>{{ errors[0] }}</span>
                    </ValidationProvider>
                  </form>
                  <br />
                  <vs-button
                    color="success"
                    type="filled"
                    :disabled="invalid"
                    @click="agregar_elemento"
                    >Agregar direcci'on</vs-button
                  >
                </ValidationObserver>
              </div>
            </vs-col>
          </vs-row> -->
          <br />
          <mapa />
        </vs-card>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
import Header from '@/pages/Header.vue'
import mapa from '@/pages/Mapa.vue'
import VueSimpleSuggest from 'vue-simple-suggest'
import 'vue-simple-suggest/dist/styles.css'
export default {
  components: {
    Header,
    mapa,
    VueSimpleSuggest
  },
  data () {
    return {
      popupActivo5: true,
      colorx: '#0a540a'
    }
  },
  methods: {
    obtenerLongitudes (nodes = []) {
      let n = nodes.length
      let longitudes = Array.from({ length: n }, () =>
        Array.from({ length: n }, () => Infinity)
      )
      for (let node of nodes) {
        if (Array.isArray(node.parent)) {
          for (let link of node.parent) {
            if (typeof link === 'object') {
              if (link.markerEnd === 'arrow') {
                longitudes[node.index][link.index] = link.value
              } else if (link.markerStart === 'arrow') {
                longitudes[link.index][node.index] = link.value
              }
            }
          }
        }
      }
      return longitudes
    },
    dijkstra (nodes = []) {
      let vm = this
      let longitudes = vm.obtenerLongitudes(nodes)
      let caminos = nodes.map((v, i) => longitudes[0][i])
      let candidatos = nodes.map((v, i) => v.index).filter(v => v > 0)
      let punteros = Array.from({ length: nodes.length }, () => 0)
      // Es suficiente con candidatos.length>1
      while (candidatos.length > 0) {
        let k = Infinity
        let min = Infinity
        for (let j of candidatos) {
          if (caminos[j] < min) {
            min = caminos[j]
            k = j
          }
        }
        candidatos = candidatos.filter(v => v !== k)
        for (let j of candidatos) {
          if (caminos[j] > caminos[k] + longitudes[k][j]) {
            caminos[j] = caminos[k] + longitudes[k][j]
            punteros[j] = k
          }
        }
      }
      return { caminos, punteros }
    }
  },

  mounted () {
    let vm = this
    let ejemplo = [
      { index: 0, parent: -1 },
      { index: 2, parent: [{ index: 0, value: 10, markerStart: 'arrow' }] },
      { index: 1, parent: [{ index: 0, value: 50, markerStart: 'arrow' }] },
      {
        index: 3,
        parent: [
          { index: 2, value: 10, markerStart: 'arrow', lineTextOffset: -1 },
          { index: 4, value: 50, markerEnd: 'arrow' },
          { index: 0, value: 100, markerStart: 'arrow', lineOffset: -1.5 },
          { index: 1, value: 20, markerEnd: 'arrow' }
        ]
      },
      {
        index: 4,
        parent: [
          { index: 1, value: 5, markerEnd: 'arrow', lineTextOffset: 1 },
          { index: 0, value: 30, markerStart: 'arrow', lineOffset: 1.5 }
        ]
      }
    ]
    console.log(vm.dijkstra(ejemplo))
  }
}
</script>
<style>
.scroll {
  overflow-x: auto;
}
.titulos {
  color: #0a540a;
  font-size: 19px;
}
</style>



