<template>
  <header class="vs-dialog-header">
    <vs-row vs-justify="center">
      <vs-col vs-w="10">
        <vs-card>
          <div slot="header" class="text-center">
            <vs-divider
              color="#0a540a"
              style="font-size: 40px; font-size: 4.0vw;"
            >
              <strong>{{ valorEjemplo }}</strong>
            </vs-divider>
            <p style="font-size: 1.8vw;">
              <strong> Universidad Autnoma del Estado de Mxico</strong>
              <br />
              Ingenieria en Sistemas Inteligentes
              <br />
              Aldo Meztas Hernndez
            </p>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>
  </header>
</template>
<script>
import ejemplo from '../Services/HolaMundo.js'
export default {
  components: {
    ejemplo
  },
  data () {
    return {
      valorEjemplo: ''
    }
  },
  mounted () {
    this.valorEjemplo = ejemplo.titulo()
  }
}
</script>



